/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import IMAGES from "../../assets/images";
// import { FaBars } from "react-icons/fa";
import { Link, useLocation } from "react-router-dom";
import { PATH } from "../../utils/path";
import { Breadcrumb, Col, Container, Row } from "react-bootstrap";

const policyPaths = [
  PATH.TERMSANDCONDITION,
  PATH.PRIVACYPOLICY,
  PATH.COOKIEPOLICY,
];
export function SimpleHeader() {
  const location = useLocation();
  const isPolicyPath = policyPaths.includes(location.pathname);

  document.body.style.backgroundColor = "#f5f5f5";

  return (
    <div
      className={`main-wrapper ${
        isPolicyPath ? "policies-header" : "simple-header"
      } d-flex align-items-center`}
    >
      <Container>
        <Row>
          <Col xs={4}>
            <Link
              to={`${isPolicyPath ? PATH.SIGNIN : PATH.DOCTOR_DASHBOARD} `}
              className="logo mr-0 header-logo-image text-decoration-none"
            >
              <img alt="sidebar logo" src={IMAGES.SIDEBAR_LOGO} />
            </Link>
          </Col>
          <Col xs={8} className="d-flex align-items-center my-auto mx-0">
            <>
              {location.pathname === PATH.APPOINTMENT_NOTES && (
                <div className="head-breadcrumb">
                  <Breadcrumb className="header-breadcrumb">
                    <Breadcrumb.Item
                      active
                      className="breadcrum_Project"
                      href="#"
                    >
                      Appointment Notes
                    </Breadcrumb.Item>
                  </Breadcrumb>
                </div>
              )}
            </>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
